@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);

.navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 0 5%;
    height: 75px;
    box-shadow: 0px 2px 29px 5px rgba(0,0,0,0.71);
    -webkit-box-shadow: 0px 2px 29px 5px rgba(0,0,0,0.71);
    -moz-box-shadow: 0px 2px 29px 5px rgba(0,0,0,0.71);
}

.navbar__title {
    margin: 0;
    text-decoration: none;
    color: var(--secondary);
    font-weight: 800;
    font-size: 22px;
}

.navbar__title:link, .navbar__title:visited, .navbar__title:active {
    text-decoration:none;
}


.navbar__list {
    list-style: none;
    padding: 0;
}

.navbar__btn {
    color: var(--secondary);
    background: none;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    padding: 10px 25px;
    transition: all .5s;
}

.navbar__btn:hover {
    background-color: var(--DarkGray);
}

@media (max-width:375px) {
    .navbar__title {
        font-size: 15px;
    }
    
}

.countries__list {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-column-gap: 4%;
    -webkit-column-gap: 4%;
            column-gap: 4%;
    grid-row-gap: 40px;
    row-gap: 40px;
}

.country {
    width: 22%;
    min-width: 260px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 1px 38px -17px rgba(0,0,0,1);
    -webkit-box-shadow: 0px 1px 38px -17px rgba(0,0,0,1);
    -moz-box-shadow: 0px 1px 38px -17px rgba(0,0,0,1);
}

.country__img {
    width: 100%;
}

.country__description {
    padding: 10px 35px;
}

.search {
    height: 120px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.search__camp--input {
    width: 380px;
    padding: 10px 20px;
    background-color: var(--primary);
    border-radius: 5px ;
    box-shadow: 0px 0px 19px -13px rgba(0,0,0,0.75);
    -webkit-box-shadow: 0px 0px 19px -13px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 19px -13px rgba(0,0,0,0.75);
    
}

.search__input {
    width: 90%;
    margin-left: 15px;
    border: none;
    outline: none;
    background: none;
}

.search__select {
    width: 170px;
    height: 42px;
    padding: 10px 5px;
}

.search__select:focus {
    outline: none;
}


@media (max-width:375px) {
    
    .countries__list {
        -webkit-flex-direction: column;
                flex-direction: column;
        grid-column-gap: 4%;
        -webkit-column-gap: 4%;
                column-gap: 4%;
        grid-row-gap: 40px;
        row-gap: 40px;
    }

    .country {
        width: 90%;
    }

    .search {
        -webkit-flex-direction: column;
                flex-direction: column;
        height: 100px;
        margin: 25px 0 35px;
    }

    .search__camp--input {
        width: 100%;
        box-sizing: border-box;
    }

    .search__input {
        width: 80%;
    }
    
    .search__select {
        width: 170px;
        height: 42px;
        padding: 10px 5px;
    }

}
.main {
    width: 90%;
    margin: 60px auto;
}

.btn {
    color: var(--secondary);
    padding: 8px 25px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: var(--primary);
    box-shadow: 0px 0px 18px -7px rgba(0,0,0,0.57);
    -webkit-box-shadow: 0px 0px 18px -7px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 0px 18px -7px rgba(0,0,0,0.57);
}

.details {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    grid-column-gap: 100px;
    -webkit-column-gap: 100px;
            column-gap: 100px;
    -webkit-align-items: center;
            align-items: center;
    margin: 70px 0;
}

.details__img {
    width: 40%;
    min-width: 350px;
}

.details__main {
    width: 45%;
}

.details__content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    grid-row-gap: 35px;
    row-gap: 35px;
}

.details__primary {
    width: 45%;
    min-width: 220px;
}

.details__secondary {
    width: 50%;
    min-width: 250px;
}

.details__tertiary {
    width: 100%;
    min-width: 300px;
}

.details__border {
    display: inline-block;
    margin: 0 5px;
    padding: 2px 10px;
    border: none;
    border-radius: 5px ;
    background-color: var(--primary);
    box-shadow: 0px 0px 18px -7px rgba(0,0,0,0.57);
    -webkit-box-shadow: 0px 0px 18px -7px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 0px 18px -7px rgba(0,0,0,0.57);
}

@media (max-width:375px) {
    .main {
        width: 85%;
        margin: 30px auto;
    }

    .details {
        -webkit-flex-direction: column;
                flex-direction: column;
        margin: 50px 0;
        grid-row-gap: 30px;
        row-gap: 30px;
    }

    .details__img {
        width: 100%;
        min-width: auto;
    }

    .details__main {
        width: 100%;
    }

    .details__content {
        grid-row-gap: 10px;
        row-gap: 10px;
    }
}
/*
- White (Dark Mode Text & Light Mode Elements): hsl(0, 0%, 100%)
- Dark Blue (Dark Mode Elements): hsl(209, 23%, 22%)
- Very Dark Blue (Dark Mode Background): hsl(207, 26%, 17%)
- Very Dark Blue (Light Mode Text): hsl(200, 15%, 8%)
- Dark Gray (Light Mode Input): hsl(0, 0%, 52%)
- Very Light Gray (Light Mode Background): hsl(0, 0%, 98%)
*/

:root {
    --white: hsl(0, 0%, 100%);
    /*Light*/
    --primary: hsl(0, 0%, 98%);
    --secondary: hsl(200, 15%, 8%);
    --DarkGray: hsl(0, 0%, 52%);
}

body {
    margin: 0;
    font-family: 'Nunito Sans', sans-serif;
    background-color: hsl(0, 0%, 98%);
    background-color: var(--primary);
    color: hsl(200, 15%, 8%);
    color: var(--secondary);
}

.home {
    width: 90%;
    margin: auto;
}

@media (max-width:375px) {
    .home {
        width: 85%;
    }
}
